<template>
  <div class="row m-4">
    <div class="col-lg-12 p-0 card">
      <div class="card-header">Blog Listesi (Tümü)</div>
      <div class="card-body">
        <table id="tabloYazilar" class="table table-responsive-lg">
          <thead>
            <tr>
              <th>Sıra</th>
              <th>Tarih (Yıl Gün Ay Saat)</th>
              <th>Başlık</th>
              <th>Görüntülenme</th>
              <th>Yorumlar</th>
              <th>Sil</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in yazi" :key="item._id">
              <td>
                <b>{{item.Sira}}</b>
              </td>
              <td width="auto">{{ item.Tarih | moment("(dddd) Do MMMM YYYY, HH:mm:ss") }}</td>
              <td>
                <b>{{item.Baslik}}</b>
              </td>
              <td>59</td>
              <td>{{ item.Yorumlar.length }}</td>
              <td>
                <button class="btn btn-danger" @click="Sil(item.Sira, index)">Sil</button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Bloglar",
  data: () => {
    return { yazi: [] };
  },
  methods: {
    async veriCek() {
      await fetch(process.env.VUE_APP_SERVER_URL)
        .then(res => {
          return res.json();
        })
        .then(res => {
          this.yazi = res;
        });
      this.tablolastir();
    },

    tablolastir() {
      $("#tabloYazilar").DataTable({
        responsive: true,
        language: {
          url: "/tr.json"
        },
        order: [[0, "desc"]],
        columnDefs: [{ width: "5%", targets: 0 }]
      });
    }
  },
  computed: {
    Sil() {
      return (id, index) => {
        fetch(`${process.env.VUE_APP_SERVER_URL}${id}`, {
          method: "DELETE",
          headers: { token: this.$session.get("token") }
        })
          .then(response => {
            this.yazi.splice(index, 1);
          })
          .catch(function(error) {
            console.log("error handled", error);
          });
      };
    }
  },
  mounted() {
    this.veriCek();
  },
  metaInfo: () => ({
    link: [
      { rel: "stylesheet", href: "https://cdn.datatables.net/1.10.20/css/dataTables.bootstrap4.min.css" },
    ],
    script: [
      {
        src: "https://cdn.datatables.net/1.10.20/js/jquery.dataTables.min.js",
        async: true,
        defer: true
      },
      {
        src:
          "https://cdn.datatables.net/1.10.20/js/dataTables.bootstrap4.min.js",
        async: true,
        defer: true
      }
    ]
  })
};
</script>

<style scoped>
thead {
  cursor: pointer;
}
</style>